/* 
primary: #85cad4
semi-dark: '#80BDC6'
dark: '#0A95A8'
*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button {
  padding: .6rem;
  border: none;
  border-radius: 5px;
  box-shadow: 0 4px 4px #00000040;
}

.btn-add {
  width: 100px;
  color: #0A95A8;
  background-color: #fff;
}

.btn-cancel {
  border: 1px solid #0A95A8 !important;
  background-color: #85cad4;
  color: #0A95A8;
  width: 100px;
}

.login {
  text-align: center;
  color: #0A95A8;
  margin: 100px;
}

.otsikko {
  margin: 10px;
}

.labelit {
  margin: 10px;
}